import React, { useEffect, useState } from "react";
import {
  Card,
  CardBody,
  CardHeader,
  Col,
  Row,
  Table,
  Button
} from "reactstrap";
import Axios from "axios";
import Cookies from "js-cookie";
import history from "../../../history";
import { Spinner } from "../../../component";

export const Item = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [itemList, setItemList] = useState([]);

  async function getData() {
    setIsLoading(true);
    const res = await Axios({
      method: "GET",
      url: `https://antarblanja.com/api/admin/item`,
      headers: { Authorization: Cookies.get("admin_token") }
    });
    setItemList(res.data);
    console.log(res.data);
    setIsLoading(false);
  }

  useEffect(() => {
    getData();
  }, []);

  return (
    <div className="animated fadeIn">
      <Spinner isLoading={isLoading} />
      <Row>
        <Col xs="12" lg="12">
          <Card>
            <CardHeader>
              <i className="fa fa-align-justify"></i> Item List
            </CardHeader>
            {itemList.length > 0 ? (
              <CardBody>
                <Button color="primary" className="float-right" onClick={()=>{history.push('/admin/add/item')}}>
                  Add
                </Button>
                <Table responsive>
                  <thead>
                    <tr>
                      <th>item id</th>
                      <th>name</th>
                      <th>kuantitas</th>
                      <th>subcategory</th>
                      <th>subcategory 2</th>
                      <th>category</th>
                      <th>action</th>
                    </tr>
                  </thead>
                  <tbody>
                    {itemList.map((item, index) => {
                      return (
                        <tr key={index}>
                          <td style={{ verticalAlign: "middle" }}>
                            {item.item_id}
                          </td>
                          <td style={{ verticalAlign: "middle" }}>
                            {item.item_name}
                          </td>
                          <td style={{ verticalAlign: "middle" }}>
                            {item.item_kuantitas + " "}
                            {item.item_satuan}
                          </td>
                          <td style={{ verticalAlign: "middle" }}>
                            {item.subcategory_name}
                          </td>
                          <td style={{ verticalAlign: "middle" }}>
                            {item.subcategory2_name}
                          </td>
                          <td style={{ verticalAlign: "middle" }}>
                            {item.category_name}
                          </td>
                          <td style={{ verticalAlign: "middle" }}>
                            <Button
                              onClick={() => {
                                alert("edit" + item.item_id);
                              }}
                            >
                              <svg
                                className="bi bi-pen"
                                width="1em"
                                height="1em"
                                viewBox="0 0 16 16"
                                fill="currentColor"
                                xmlns="http://www.w3.org/2000/svg"
                              >
                                <path
                                  fillRule="evenodd"
                                  d="M5.707 13.707a1 1 0 01-.39.242l-3 1a1 1 0 01-1.266-1.265l1-3a1 1 0 01.242-.391L10.086 2.5a2 2 0 012.828 0l.586.586a2 2 0 010 2.828l-7.793 7.793zM3 11l7.793-7.793a1 1 0 011.414 0l.586.586a1 1 0 010 1.414L5 13l-3 1 1-3z"
                                  clipRule="evenodd"
                                />
                                <path
                                  fillRule="evenodd"
                                  d="M9.854 2.56a.5.5 0 00-.708 0L5.854 5.855a.5.5 0 01-.708-.708L8.44 1.854a1.5 1.5 0 012.122 0l.293.292a.5.5 0 01-.707.708l-.293-.293z"
                                  clipRule="evenodd"
                                />
                                <path d="M13.293 1.207a1 1 0 011.414 0l.03.03a1 1 0 01.03 1.383L13.5 4 12 2.5l1.293-1.293z" />
                              </svg>
                            </Button>
                          </td>
                        </tr>
                      );
                    })}
                  </tbody>
                </Table>
              </CardBody>
            ) : (
              <p style={{ margin: 20, alignSelf: "center" }}>No Dropzone Yet</p>
            )}
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default Item;
